@import "../styles/variables";

.sidebar {
  position: fixed;
  top: 0;
  right: 0;
  width: 340px;
  height: 100vh;
  transform: translate(350px);
  background-color: black;
  transition: transform $transition-duration;

  &--opened {
    transform: translate(0);
  }

  &__close {
    position: absolute;
    border: none;
    top: 45px;
    right: 45px;
    height: 29px;
    width: 29px;
    cursor: pointer;
    transform: scale(1);
    background: url("/img/close.svg") no-repeat;
    transition: transform $transition-duration;
    outline: none;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__buy {
    position: absolute;
    bottom: 40px;
    right: 40px;
    padding: 20px 94px;
    border: 2px solid white;
    background-color: transparent;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2px;
    color: white;
    cursor: pointer;
  }
}
