.navigation {
  display: flex;
  flex-direction: column;
  width: 200px;

  &__list {
    list-style: none;
  }

  &__item {
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.67px;
    text-transform: uppercase;
    margin-bottom: 30px;

    &--active {
      font-weight: 500;
    }
  }
}
